@import "../../styles/AppDesign.scss";

.animated-text{
    position: absolute;
    top:50%;
    left:0;
    color:#FFF;
    transform: translateY(-50%);
}

$animation_time: .45s;
$animation_scale: 80%;

.animated-text__content{
    opacity:0;
    .animated-text.mod-active &{
        animation: animated-text-active $animation_time .05s forwards; 
    }
    .animated-text.mod-previous &{
        animation: animated-text-previous $animation_time forwards; 
    }
}


@keyframes animated-text-active {
    0% {
        opacity:0;
        transform: translate($animation_scale, 0);
    }
    100% {
        opacity:1;
        transform: translate(0, 0);
    }
}
@keyframes animated-text-previous {
    0% {
        opacity:1;
        transform: translate(0, 0);
    }
    100% {
        opacity:0;
        transform: translate(0, -$animation_scale);
    }
}