
// Colors
$main: #e5007e;
$black: #000000;
$white: #ffffff;
$dark: #1d1d1b;

$lightGray:#f8f8f8;
$lighterGray:#eeeeee;
$lineGray:#e8e1e0;
$mediumGray: #b6b4b4;
$darkerGray:#757573;

$darkGray: #747474;

$bgLightGray:$lightGray;
$bgMediumGray: #f4f1ef;
$bgGray:$lighterGray;

$lightPink:rgba(#e5007e, .04);
$green: #206d2c;

// Global Values
$gridBig: 1500px;
$gridMedium: 1280px;
$gridDesktop: 560px;
$cardGutter:30px;
$headerHeightL: 90px;
$headerHeightM: 70px;
$headerHeightS: 55px;

// Fonts Size:
$fontSizeTitleXXL:2.5rem;
$fontSizeTitleXL:2rem;
$fontSizeTitleL:1.8rem;
$fontSizeTitleM:1.5rem;
$fontSizeTitleS:1.4rem;
$fontSizeTitleSS:1.2rem;
$fontSizeTitleXS:1rem;

$fontSizeXXL:2.5rem;
$fontSizeXL:1.3rem;
$fontSizeL:1.125rem;
$fontSizeM:1rem;
$fontSizeS:.85rem;
$fontSizeSS:.75rem;
$fontSizeXS:.5rem;

// Fonts
@include fontFace("Avenir-Medium", "/fonts/Avenir-Medium");
@include fontFace("Didot-Bold", "/fonts/Didot-Bold");
@include fontFace("Lavanda-Regular", "/fonts/Lavanda-Regular");

@include fontFace("Avenir", "/fonts/Avenir-Light", normal, 100);
@include fontFace("Avenir", "/fonts/Avenir-Book", normal, 300);
@include fontFace("Avenir", "/fonts/Avenir-Regular", normal, 400);
@include fontFace("Avenir", "/fonts/Avenir-Medium", normal, 600);
@include fontFace("Avenir", "/fonts/Avenir-Heavy", normal, 800);
@include fontFace("Avenir", "/fonts/Avenir-Black", normal, 900);


$fontPrimary: "Didot-Bold", -apple-system,
BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
"Fira Sans", "Droid Sans", "Helvetica Neue",
"Microsoft JhengHei", 微軟正黑體,
sans-serif;

$fontSecondary: "Avenir", -apple-system,
BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
"Fira Sans", "Droid Sans", "Helvetica Neue",
"Microsoft JhengHei", 微軟正黑體, 
sans-serif;

$fontCardName: "Lavanda-Regular", -apple-system,
BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
"Fira Sans", "Droid Sans", "Helvetica Neue",
"Microsoft JhengHei", 微軟正黑體, 
sans-serif;