@import "../../styles/AppDesign.scss";

.home-banner{
    position: relative;
    height: 100vh;
    width: 100%;
    background:url('/img/bg.jpg') no-repeat center center;
    background-size:cover;
}
.home-banner__wrapper{
    position:relative;
    width:90%;
    height: 100%;
    max-width: 1480px;
    margin:0 auto;
}
.home-banner__content{
    position:relative;
    width:100%;
    height: 100%;
    max-width:640px;
}
.home-banner__text{
    font-size: 4rem;
    line-height: 1.3em;
    span{
        display:block;
        font-weight: 100;
    }
    strong{
        display:block;
        font-weight: 900;
    }
}
.home-banner__control{
    position: absolute;
    width:100%;
    text-align: right;
    bottom:60px;
}
.home-banner__btn-prev,
.home-banner__btn-next{
    position: relative;
    margin-right:1rem;
    $size:45px;
    width:$size;
    height:$size;
    background: url('/img/left-arrow.svg') no-repeat center center;
    background-size: contain;
    border:0;
    transition: .2s;
    cursor: pointer;
    &:hover{
        opacity:.5;
    }
}
.home-banner__btn-next{
    transform: rotate(180deg);
}