
@mixin fontFace($family, $src, $style: normal, $weight: normal) {
  @font-face {
    font-family: $family;
    src: url("#{$src}.eot"); // IE9 compat
    src: url("#{$src}.eot?#iefix") format("embedded-opentype"),
      // IE8 and below
        url("#{$src}.woff") format("woff"),
      // standards
        url("#{$src}.ttf") format("truetype"),
      // Safari, Android, iOS
        url("#{$src}.svg##{$family}") format("svg"); // legacy iOS

    font-style: $style;
    font-weight: $weight;
  }
}
@mixin a11y-hidden {
  position:absolute;
    left:-10000px;
    top:auto;
    width:1px;
    height:1px;
    overflow:hidden;
}
@mixin global-content-grid(){
  max-width:$gridMedium;
  width:90%;
}

@mixin desktop-content-grid(){
  max-width: 560px;
  width:100%;
}

@mixin gray-card(){
  width: 100%;
  background-color: $dark;
  padding: 1rem;
  border-radius: 0.7rem;
  max-width: 480px;
}

@mixin hamburger-icon() {
  position: relative;
  $size:30px;
  $lineHeight:3px;
  width:$size;
  height:$size;
  @include smallScreen(){
    $size:25px;
    width:$size;
    height:$size;
  }
  border:0;
  background: transparent;
  font-size:0;
  padding:0;
  cursor: pointer;
  
  &>span{
    position: relative;
    display:block;
    width:100%;
    height:100%;
    top:0;
    left:0;
    transition: .4s;

    &:before,
    &:after{
        content:'';
        display:block;
        position:absolute;
        width:100%;
        left:0;
        height:$lineHeight;
        margin-top:-$lineHeight/2;
        background:#000;
        transition: .4s;
    }
    &:before{
        top:30%;
    }
    &:after{
        top:70%;
    }
  }
  &.mod-open{
    &>span{
      transform: rotate(45deg);
      &:before,
      &:after{
        top:50%;
        $lineHeight:2px;
        height:$lineHeight;
        margin-top:-$lineHeight/2;
      }
      &:after{
        transform: rotate(90deg);
      }
    }
  }
}