@import "./styles/normalize.scss";
@import "./styles/AppDesign.scss";

html,
body {
  height: 100%;
}

html,
body,
#root {
  width: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-touch-callout: none;
}

#root {
  overflow-x: hidden;
}
main {
  width: 100%;
  overflow-x: hidden;
}

input,
button {
  outline: none;
  -webkit-appearance: none;
}

html {
  font-family: $fontSecondary;
}

body {
  // overflow: hidden;
  // position: fixed;
  background: $lightGray;
  margin: 0;
  font-family: $fontSecondary;
  -moz-osx-font-smoothing: grayscale;
  color: $darkGray;

  &.ReactModal__Body--open {
    overflow: hidden;
    position: fixed;
    width: 100%;
    height: 100%;
  }
}

* {
  box-sizing: border-box;
}

// Modal Style
.ReactModalPortal {
  position: relative;
  z-index: 5000;
}

.ReactModal__Content {
  z-index: 5000;
  display: table;
  height: 100%;
  width: 100%;
  margin: 0 auto;

  .modal {
    &-container {
      position: relative;
      width: 100%;
      display: table-cell;
      vertical-align: middle;
      text-align: center;
      padding: 0;
      font-size: 0;
    }

    &-container-inner {
      position: relative;
      display: inline-block;
      box-sizing: border-box;
      background-color: $white;
      box-shadow: 0px 0px 20px 15px rgba(0, 0, 0, 0.15);
      padding: 5rem 5%;
      @include global-content-grid();
      max-width: $gridDesktop;
      max-height: 90vh;
      overflow-y: auto;
      @include smallScreen() {
        padding: 0rem 4% 1.5rem 4%;
      }
      border-top: solid 0.5rem black;
    }

    &-content {
      width: 100%;
    }

    &-close {
      $crossSize: 24px;
      $lineWidth: 3px;
      position: absolute;
      top: 1rem;
      right: 1rem;
      width: 2rem;
      height: 2rem;
      background: transparent;
      border: 0;
      cursor: pointer;

      &:disabled {
        opacity: 0.5;
      }

      &-label {
        @include a11y-hidden();
      }

      &-graphic {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        transform: rotate(45deg);

        &:before,
        &:after {
          content: "";
          position: absolute;
          display: block;
          background: $darkGray;
        }

        &:before {
          width: $lineWidth;
          height: $crossSize;
          left: 50%;
          top: 50%;
          margin-left: -$lineWidth/2;
          margin-top: -$crossSize/2;
        }

        &:after {
          height: $lineWidth;
          width: $crossSize;
          right: 50%;
          top: 50%;
          margin-top: -$lineWidth/2;
          margin-right: -$crossSize/2;
        }
      }
    }
  }
}

.ReactModal__Overlay {
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  transition: opacity 500ms ease-in-out;
  background-color: rgba(255, 255, 255, 0.7);

  &--after-open {
    opacity: 1;
  }

  &--before-close {
    opacity: 0;
  }
}

// Form
.custom-form {
  position: relative;
  padding: 2rem 0;
}
.custom-form-inner {
  @include global-content-grid;
  max-width: 480px;
  position: relative;
  margin: 0 auto;
}
.custom-form-row {
  margin-top: 1rem;
  &.mod-align-left {
    text-align: left;
  }
  &.mod-align-right {
    text-align: right;
  }
  &.mod-align-center {
    text-align: center;
  }
}
.custom-form-field {
  position: relative;
  width: 100%;
  margin-bottom: 2rem;
}
.custom-form-field-title {
  font-size: $fontSizeS;
  margin: 0 0 0.25rem;
}
.custom-form-field-body {
  position: relative;
  width: 100%;
  input {
    width: 100%;
    background: transparent;
    padding: 0.5rem 0;
    border: 0;
    border-bottom: 1px solid $mediumGray;
    height: 40px;
  }
  .custom-form-field-btn {
    position: absolute;
    font-size: $fontSizeS;
    height: 100%;
    right: 0;
    top: 0;
    display: flex;
    align-items: center;

    &:hover {
      color: $main;
    }
  }
}
.custom-form-field-btn {
  border: 0;
  box-shadow: none;
  background: transparent;
  appearance: none;
  cursor: pointer;
  color: $mediumGray;
  font-size: $fontSizeSS;
}

.custom-form-control {
  margin-top: 0;
  position: relative;
  width: 100%;
  text-align: center;
}
.custom-form-btn {
  width: 100%;
  background: #000;
  color: #fff;
  appearance: none;
  border: 0;
  border-radius: 5px;
  padding: 1rem;
  font-size: $fontSizeS;
  cursor: pointer;
}
.custom-form-btn-white {
  width: 100%;
  background: #fff;
  color: #000;
  font-weight: 600;
  appearance: none;
  border: solid 3px $black;
  border-radius: 8px;
  padding: 1rem;
  font-size: $fontSizeS;
  cursor: pointer;
  text-decoration: none;
}
.custom-form-link {
  color: #000;
  text-decoration: underline;
  font-size: $fontSizeSS;
}
.custom-form-field-textarea {
  width: 100%;
  outline: none;
  resize: none;
  border-radius: 0.6rem;
  border-color: $lineGray;
  margin-top: 0.8rem;
}
.custom-form-field-error {
  font-size: $fontSizeSS;
  color: red;
}

.remove-link-default {
  text-decoration: none;
}

.remove-button-default{
  border: none;
  background-color: transparent;
}

.align-center{
  display: flex;
  justify-content: center;
  align-items: center;
}
// Global Helpers
.a11y-hidden {
  @include a11y-hidden();
}
.hidden-desktop {
  @include desktopView() {
    display: none !important;
  }
}
.hidden-mobile {
  @include mobileView() {
    display: none !important;
  }
}
.visible-desktop {
  @include mobileView() {
    display: none !important;
  }
}
.visible-mobile {
  @include desktopView() {
    display: none !important;
  }
}
